<template>
<section class="define-perspolise">
  <div class="container mx-auto p-1">
    <div class="w-full h-auto">
      <div v-for="value in menu" :key="value">
        <slot name="myImage"><v-lazy-image @error="notShow" style="height: 100px" :src="`${value.news[counter].news_image[0].url}`" alt="" class="w-full"/></slot>
        <div class="overflow-hidden" style="height: 46px">
          <slot name="text"><p class="title font-thin text-start text-black" :style="{fontSize:size+'px',height:textHeight+'px'}">{{value.news[counter].title}}</p></slot>
        </div>
        <div class="flex flex-row align-center justify-between">
          <div>
            <p class="font-thin text-start text-slate-800 " :style="{fontSize:sizeText+'px'}">2 ساعت قبل</p>
          </div>
          <div class="flex flex-row">
            <div class="flex flex-row">
              <font-awesome-icon :icon="['fas','comment']" class="text-slate-400 text-center  text-sm font-semibold" :style="{fontSize:sizeText+'px'}"></font-awesome-icon>
              <span class="text-slate-400 font-thin " :style="{fontSize:sizeText+'px'}">{{value.news[counter].news_comment_count}}</span>
            </div>
            <div class="flex flex-row">
              <font-awesome-icon :icon="['fas','eye']" class="text-slate-400 text-center  text-sm font-semibold" :style="{fontSize:sizeText+'px'}"></font-awesome-icon>
             <span class="text-slate-400 font-thin text-sm" :style="{fontSize:sizeText+'px'}">{{value.news[counter].visit}}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</section>
</template>

<script>
import VLazyImage from "v-lazy-image";
import {FontAwesomeIcon} from "@fortawesome/vue-fontawesome";
import {ref} from 'vue'
export default {
  name: "DefinePerspolise",
  props:{
    menu:{
      type:Array,
      required:true,
      default:null
    },
    counter:{
      type:Number,
      required:true,
      default: null
    }
  },
  components:{VLazyImage,FontAwesomeIcon},
  setup(){
    const size=ref(10);
    const textHeight=ref(50)
    const sizeText=ref(10);
    const notFound=ref('https://footballi.net/assets/shared-application/images/placeholders/ic_default_news_image.png')
    function notShow(event){
      event.target.src=notFound.value
    }
    return{
    notFound,
      size,
      sizeText,
      textHeight,
      notShow
    }
  }
}
</script>

<style scoped>
.title{
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: normal;
}
</style>